<template>
  <div class="main-padding">
    <el-card>
      <el-form :inline="true">
        <el-form-item>
          <el-button @click="signUp">注册</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-form :model="query" :inline="true">
        <el-form-item>
          <el-input
            v-model="query.name"
            placeholder="用户名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="query.phone"
            placeholder="手机"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="changePage(1)">查询</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card style="margin-top: 20px;">
      <el-table border style="width: 100%" :data="list">
        <el-table-column label="头像" width="142">
          <template slot-scope="scope">
            <a
              :href="`http://localhost:3000/user/` + scope.row.id"
              target="_blank"
            >
              <img
                v-lazy="$img.head(scope.row.head)"
                style="width: 120px;height: 120px;"
              />
            </a>
          </template>
        </el-table-column>
        <el-table-column label="姓名" prop="name"></el-table-column>
        <el-table-column label="手机" width="150" prop="phone">
        </el-table-column>
      </el-table>
      <el-pagination
        ref="pagination"
        layout="prev, pager, next, jumper"
        background
        :page-size="pageable.size"
        :total="totalElements"
        :current-page="pageable.page"
        @current-change="changePage"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
import { Pageable } from "../../assets/script/model"
import { QueryForm } from "./script/model"
import qs from "qs"
import { mapActions } from "vuex"

export default {
  data() {
    return {
      pageable: new Pageable(),
      query: null,
      list: [],
      totalElements: 0
    }
  },
  watch: {
    $route: {
      handler(to) {
        this.pageable.page = parseInt(to.params.page)
        this.pageable.sort = "createDate,desc"
        this.query = new QueryForm(to.query)
        this.paging()
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions("user", ["APaging", "ASignUp"]),
    changePage(page) {
      let query = Object.assign({}, this.query)
      query.date = query.date ? query.date.join(",") : []
      this.$router.push(`/user/${page}?${qs.stringify(query)}`)
    },
    async paging() {
      let loading = this.$loading()
      let paging = (
        await this.APaging(Object.assign({}, this.pageable, this.query))
      ).data
      loading.close()
      this.list = paging.content || []
      this.totalElements = paging.totalElements || 0
      document.getElementsByTagName("main")[0].scrollTo(0, 0)
    },
    signUp() {
      this.$prompt("请输入所谓的手机号", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(async ({ value }) => {
          let loading = this.$loading()
          let result = await this.ASignUp({ phone: value })
          loading.close()
          if (result.status === 200) {
            this.$notify({ message: "成功" })
            this.getList()
          } else {
            this.$notify({ message: result.message })
          }
        })
        .catch(() => {})
    }
  }
}
</script>

<style type="text/less" lang="less" scoped></style>
